       	let passions_scene = new ScrollMagic.Scene({triggerElement: "#content_passions"})
			.on("enter", function () {
                app.show_passions_images = true;
            })
			.addTo(controller_3);
    },
    methods: {
        setPercentajesToBar() {
            let percentages = document.getElementsByClassName('percentage-number');
    
            for (let i = 0; i < percentages.length; i++) {
                percentages[i].style.width = percentages[i].getAttribute('percentage-number') + '%';
            }
        }
    }
}
